.App {
  display: flex;
  flex-direction: column;
}

.header {
  padding: 1rem 2rem;
  background: #3b5998;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header h1 {
  color: #fff;
}

.header select {
  height: 30px; width: 140px;
  border-radius: 5px;
  border: none;
}

section {
  padding: 1rem;
}

.contents {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
}

html,body,p,h1,h2,h3{margin:0px;}
table{border-collapse: collapse;}


h1{text-align: center;}
h2{margin-bottom:20px;}
header{padding:20px 0;}
header p{
  display:block;
  width:50%; height:5%;
  margin:0 auto; padding:30px;
  border:1px solid #ddd;
  border-radius: 20px;
  background:#fff;
  box-shadow: 0px 0px 5px #ccc;
  color:#000; text-align:center;
  font-size:1.2em; font-weight: bold;
}
main{width:768px; margin:0 auto;}
main .ko{}

main .ko div{
  width:100%;
  /* display: table; */
  background:#fff;
  display:flex;
  border:1px solid #ddd;
  border-radius: 20px;
  /* border-collapse: collapse; */
  text-align:center;
  box-shadow: 0px 0px 5px #ccc;
}
main .ko div:nth-of-type(1){
  font-size:1em; font-weight:bold;
}
main .ko div span{
  /* display:table-cell; */
  
  padding:30px; width:33%;
}
main .ko div:nth-of-type(2){
  background:#fff; margin-top:10px;
}
main .ko div:nth-of-type(2) span{padding:15px;}

/* 지역별 */
main .area{/* height:100%; */}
main .area > div{
  border-radius: 20px;
  border:1px solid #ddd; background:#fff;
  text-align:center; padding:40px;
  box-shadow: 0px 0px 5px #ccc;
}
main .area > div:nth-of-type(2){padding:0; margin-top:30px;}
main .area div table{
  width:100%; background:#fff; 
  margin-top:20px;
}
main .area div table caption{
  margin:20px; 
  font-size:3em; font-weight:bold;
}
main .area div table th,
main .area div table td{
  padding:15px; border:1px solid #000;
}

.recharts-responsive-container{ width:100% !important; height:300px !important;}